.App {
    text-align: center;
  }
  
  * {
    font-family: sans-serif;
  }
  input,
  button {
    font-size: 20px;
    padding: 10px;
    border-radius: 5px;
  }
  input ,.sum{
    display: block;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: 52%;
  }
  button {
    border: 1px solid gray;
    background: whitesmoke;
    margin-right: 5px;
  }
  button:nth-last-child(2),
  button:nth-last-child(1) {
    background: tomato;
    color: white;
  }
  
  
  